import { addArticles } from "@/utils/request";
export default {
  middleware: "authenticated",
  name: "editor",
  data() {
    return {
      article: {
        title: "",
        description: "",
        body: "",
        tagList: "",
      },
    };
  },
  methods: {
    async submit() {
      try {
        const { data } = await addArticles({ article: this.article });
        const { slug } = data.article;
        this.$router.push({ path: `/article/${slug}` });
      } catch (err) {
        throw new Error("fail");
      }
    },
  },
};
